// unfortunately the shake event does work on iOS. Let's just work with the click then...

window.addEventListener('click', switchCaracters)

function switchCaracters() {
    if(clickCount !== 3) {
        ben.classList.toggle('visible')
        ben.classList.remove('init')
        cat.classList.toggle('visible')
        cat.classList.remove('easter-egg')
        ben.classList.remove('easter-egg')
        button.classList.remove('init')

    } else {
        ben.classList.add('visible')
        cat.classList.remove('visible')
        cat.classList.add('easter-egg')
        ben.classList.add('easter-egg')
    }


    clickCount++
}

